a {
	text-decoration: none; }
header {
	position: relative;
	height: 50px; }
nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 2; }

.hamburger {
	display: none; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	&_link {
		color: #ffffff;
		font-size: 15px;
		font-weight: 300; }
	&_item {
		border-right: 2px dotted black;
		padding-right: 28px;
		&:last-child {
			border-right: none; } } }
.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0,0,0, .6);
	&_logo {
		display: block;
		max-width: 100%;
		margin-top: 15px;
		img {
			max-width: 45%;
			filter: invert(1); } }
	&_official {
		margin-top: 6px;
		opacity: 0.5;
		color: #ffffff;
		font-size: 13px;
		font-weight: 400; }
	&_call {
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		margin-top: 19px;
		text-align: right; }
	&_phone {
		display: block;
		margin-top: 8px;
		color: #ffffff;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 12px; }
	&_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }


.promo {
	min-height: 800px;
	background: url('../img/bg/uber_bg.jpg') center center/cover no-repeat;
	padding: 150px 0 135px 0;
	&_header {
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 60px;
		line-height: 60px;
		margin: 0;
		text-align: center; }
	&_subheader {
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 36px;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }
	&_descr {
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: center;
		margin-top: 23px; }
	&_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $color_blue 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }
.reasons {
	padding: 71px 0 65px 0;
	.reasons_block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px;
		.reasons_round {
			display: flex;
			justify-content: center;
			align-items: center;
			margin-right: 26px;
			width: 113px;
			height: 113px;
			background-color: $color_blue;
			border-radius: 100%; }
		.reasons_descr {
			width: 343px;
			.reasons_subtitle {
				line-height: 21px;
				color: $color_blue;
				font-size: 17px;
				font-weight: 400; }
			.reasons_text {
				color: #202020;
				font-size: 15px;
				font-weight: 300;
				line-height: 21px; } } } }
.label {
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center; }
.title {
	margin-bottom: 0;
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center; }

.subtitle {
	color: #222222;
	margin-top: 19px;
	font-size: 17px;
	font-weight: 400;
	text-align: center; }
.mobile {
	min-height: 865px;
	padding: 48px 0 80px 0;
	background: url('../img/bg/mobile_bg.jpg') center (center / cover) no-repeat;
	.row {
		margin-top: 32px;
		margin-bottom: 19px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		&_1 {
			background: url('../img/bg/mobile_screen_1.jpg') center (center / cover) no-repeat; }
		&_2 {
			background: url('../img/bg/mobile_screen_2.jpg') center (center / cover) no-repeat; }
		&_3 {
			background: url('../img/bg/mobile_screen_3.jpg') center (center / cover) no-repeat; }
		&_4 {
			background: url('../img/bg/mobile_screen_4.jpg') center (center / cover) no-repeat; }
		&_5 {
			background: url('../img/bg/mobile_screen_5.jpg') center (center / cover) no-repeat; }
		&_6 {
			background: url('../img/bg/mobile_screen_6.jpg') center (center / cover) no-repeat; }
		&_subtitle {
			color: #fff;
			font-size: 24px;
			font-weight: 400;
			text-transform: uppercase;
			line-height: 31px;
			text-align: center; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			background-color: #000000;
			margin: 0 auto;
			border-radius: 100%;
			cursor: pointer;
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 2px;
				background-color: $color_blue;
				transition: 0.3s all; }
			&:after {
				content: '';
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				display: block;
				height: 14px;
				width: 2px;
				background-color: $color_blue;
				transition: 0.3s all; }
			&:hover {
				&:before, &:after {
					background-color: $color_red; } } } } }

.change {
	min-height: 850px;
	background: url('../img/bg/black_bg.jpg') top no-repeat ,
	url('../img/bg/mobile_bg_min.jpg') bottom no-repeat {}
	padding: 55px 0px 137px 0px;
	.label {
		background-color: #fff;
		color:#000 {} }
	.title {
		margin-top: 27px;
		padding: bottom 0px;
		color: #fff; }
	.subtitle {
		color: #fff;
		margin-top: 0; }
	img {
		display: block;
		margin: 0 auto;
		margin-top: 55px; }
	&_descr {
		display: block;
		margin: 0 auto;
		width: 327px;
		min-height: 49px;
		text-align: center;
		font-weight: 300;
		font-size: 17px;
		line-height: 24px;
		color: #222222; } }

.requiere {
	padding: 68px 0 84px 0;
	&_block {
		margin: 33px 0;
		&_nmb {
			margin-bottom: 0px; } }
	&_title {
		color: #1ca8c3;
		font-size: 22px;
		font-weight: 400; }
	&_list {
		list-style-type: none;
		padding-left: 50px;
		margin-top: 22px;
		li {
			position: relative;
			font-size: 17px;
			font-weight: 300;
			line-height: 24px;
			margin-bottom: 10px;
			&::before {
				content: '';
				display: block;
				position: absolute;
				left: -32px;
				top: 50%;
				transform: translateY(-50%);
				width: 21px;
				height: 20px;
				background: url('../icons/check_blue.svg') no-repeat; } } }
	&_descr {
		font-weight: 300;
		font-size: 17px;
		line-height: 24px;
		color: $color_blue; }
	.warning {
		.requiere_title {
			color: $color_red; }
		.requiere_list {
			li {
				&::before {
					background: url('../icons/check_red.svg') no-repeat; } } } } }

.world {
	position: relative;
	height: 716px;
	background: #080816 url('../img/bg/map.png') center (center / cover) no-repeat;
	background-size: 1440px;
	padding: 50px 0 50px 0;
	.label {
		background-color: #fff;
		color:#000 {} }
	.title {
		margin-top: 27px;
		padding: bottom 0px;
		color: #fff; }
	.subtitle {
		color: #fff;
		margin-top: 0; }
	&_town_link {
		display: block;
		bottom: 50px;
		left: 50%;
		transform: translateX(-50%);
		position: absolute;
		font-weight: 400;
		font-size: 15px;
		text-transform: uppercase;
		color: #1FBAD6; } }
footer {
	height: 165px;
	padding: 12px 20px 45px 20px;
	position: relative;
	.divider {
		display: block;
		margin: 0 auto;
		width: 300px;
		height: 7px;
		background-color: #1FBAD6; }
	.footer_main_link {
		margin-top: 33px;
		list-style-type: none;
		display: flex;
		justify-content: center;
		li {
			padding-right: 27px;
			a {
				display: block;
				text-transform: uppercase;
				font-weight: 300;
				font-size: 15px;
				color: #070716; } } }
	.footer_secondary_link {
		list-style-type: none;
		display: flex;
		justify-content: center;
		li {
			padding-right: 15px;
			a {
				display: block;
				text-transform: uppercase;
				font-weight: 300;
				font-size: 11px;
				color: #999999; } } }
	.language {
		display: block;
		text-align: center;
		font-weight: 400;
		font-size: 11px;
		text-transform: uppercase;
		color: #1FBAD6; }
	.social_media {
		display: flex;
		position: absolute;
		top: 50px;
		&_link {
			list-style-type: none;
			display: flex;
			margin: 0; }
		&_items {
			display: block;
			padding-right: 10px;
			img {
				display: block;
				width: 18px; } } }
	.donload {
		display: flex;
		position: absolute;
		top: 40px;
		right: 5px;
		&_link {
			list-style-type: none;
			display: flex; }
		&_items {
			display: block;
			padding-right: 10px;
			img {
				display: block;
				width: 85px; } } } }


.xs-visible {
	display: none; }
