@media (max-width: 1200px) {
    .menu {
        &_item {
            padding-right: 20px; }
        &_link {
            font-size: 13px; } }
    .promo {
        &_header {
            font-size: 50px; } }
    .reasons {
        .reasons_block {
            align-items: flex-start;
            .reasons_descr {
                width: 300px; } } }
    .mobile {
        &_item {
            width: 300px; } } }
@media (max-width: 991px) {
    .menu {
        &_item {
            padding-right: 8px; }
        &_link {
            font-size: 10px; } }
    .subheader {
        &_logo {
            img {
                width: 100%; } }
        &_official {
            font-size: 11px; }
        &_phone {
            font-size: 18px; }
        &_btn {
            margin-top: 21px;
            height: 40px;
            width: 100%; } }
    .promo {
        &_header {
            font-size: 40px; }
        &_subheader {
            font-size: 30px; } }
    .reasons {
        .reasons_block {
            flex-direction: column;
            align-items: center;
            .reasons_round {
                margin-right: 0; }
            .reasons_descr {
                width: 300px;
                margin-top: 20px; } } }
    .change {
        img {
            width: 100%; } }
    footer {
        height: 255px;
        .donload_link {
            display: flex;
            flex-direction: column;
            li {
                margin-top: 5px; } } } }


@media (max-width: 767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0,0,0,.8);
        transition: 0.6s all;
        &_active {
            left: 0; }
        &_item {
            margin: 10px 0;
            padding-right: 0;
            border: none; }
        &_link {
            font-size: 20px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #fff;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(3px) rotate(45deg); } } } }
    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; }

    .subheader {
        &_phone {
            display: block;
            margin-top: 10px;
            color: #fff;
            font-size: 16px;
            font-weight: 400;
            text-transform: uppercase;
            text-align: center; }
        &_btn {
            display: block;
            width: 200px;
            height: 40px;
            margin: 0 auto;
            margin-top: 10px; } }
    .promo {
            &_header {
                line-height: 45px; } }
    .change {
        img {
            width: 100%; } }
    footer {
        position: relative;
        .donload {
            display: flex;
            position: absolute;
            bottom: 20px;
            right: 20px;
            .donload_link {
                list-style-type: none;
                display: flex;
                align-items: flex-end;
                flex-direction: row; }
            &_items {
                display: block;
                padding-right: 10px;
                img {
                    display: block;
                    width: 85px; } } }
        .social_media {
            display: flex;
            position: absolute;
            top: 195px;
            .social_media_link {
                list-style-type: none;
                display: flex;
                margin: 0;
                align-items: flex-end;
                flex-direction: row; }
            &_items {
                display: block;
                padding-right: 10px;
                img {
                    display: block;
                    width: 18px; } } } } }




@media (max-width: 575px) {
    nav {
        height: 30px; }
    header {
        height: 30px; }
    .subheader {
        top: 30px;
        height: 60px;
        &_official {
            display: none; }
        &_logo {
            img {
                margin-top: 5px;
                max-width: 100px; } }
        &_phone {
            font-size: 12px;
            font-weight: 400;
            text-transform: uppercase;
            margin-top: 11px; }
        &_btn {
            width: 110px;
            height: 22px;
            font-size: 10px;
            margin-top: 6px; } }
    .promo {
        min-height: 400px;
        padding: 93px 0 35px 0;
        &_descr {
            display: none; }
        &_header {
            font-size: 28px;
            padding: 0 15px;
            line-height: 35px; }
        &_subheader {
            font-size: 18px;
            margin-top: 33px;
            padding: 0 8px;
            line-height: 30px; }
        &_btn {
            width: 210px;
            height: 50px;
            margin-top: 72px; } }
    .menu {
        width: 85%; }
    .reasons {
        padding: 22px 0 36px 0;
        &_block {
            margin-top: 26px; }
        &_round {
            width: 100px;
            height: 100px; }
        &_descr {
            width: 100%;
            margin-top: 27px; }
        &_subtitle {
            font-size: 17px; } }

    .title {
        font-size: 15px;
        margin-top: 15px;
        line-height: 15px; }
    .change {
        padding: 55px 0 0 0;
        min-height: 500px;
        img {
            width: 80%; }

        .title {
            display: none; }
        .subtitle {
            display: none; }
        &_descr {
            color: #fff;
            font-weight: 300;
            font-size: 17px;
            line-height: 24px; } }
    .requiere {
        li {
            margin-bottom: 2px; } }
    .world {
        height: 360px;
        padding: 0;
        background: url('../img/bg/map.png') center (center / cover) no-repeat;
        &_town_link {
            bottom: 35px;
            font-size: 13px; }
        .label {
            margin-top: 25px; } }
    footer {
        height: 254px;
        padding: 25px 5px 30px 5px;
        .divider {
            height: 5px;
            width: 100%; }
        .footer_main_link {
            margin-top: 22px;
            flex-wrap: wrap;
            li {
                padding-right: 10px; } }
        .footer_secondary_link {
            flex-wrap: wrap;
            li {
                padding-right: 20px; } }
        .donload {
            display: flex;
            position: absolute;
            right: 0;
            margin: 210px auto;
            padding: 0;
            .donload_link {
                align-items: flex-end;
                flex-direction: row;
                img {
                    width: 70px; } } }
        .social_media {
            top: 0;
            margin: 200px auto; } } }










